<template>
  <div class="page">
    <div class="content content-better-name floated-right">
      <h2>Hash kd ltrehozsa (Tanstvnyok)</h2>
      <div>
        <b-form-input
          class="input-field-for-search"
          id="filter-input"
          v-model="filter"
          type="search"
          placeholder="Keress Hallgat neve alapjn"
        ></b-form-input>
        <b-table
          striped
          id="table"
          table-variant="light"
          selectable
          @row-selected="onRowSelected"
          :fields="fields"
          :items="items"
          :per-page="perPage"
          :current-page="currentPage"
        >
          <template #thead-top="">
            <b-tr>
              <b-th colspan="1"><span class="sr-only"></span></b-th>
              <b-th variant="secondary" colspan="3">Kpz</b-th>
              <b-th variant="light" colspan="2">Kpzett</b-th>
              <b-th variant="secondary" colspan="3">Vizsga</b-th>
            </b-tr>
          </template>

          <template #cell(selected)="{ rowSelected }">
            <template v-if="rowSelected">
              <span class="check-mark" aria-hidden="true">&check;</span>
              <span class="sr-only">Selected</span>
            </template>
            <template v-else>
              <span aria-hidden="true">&nbsp;</span>
              <span class="sr-only">Not selected</span>
            </template>
          </template>

          <template #cell(show_details)="row">
            <b-button size="sm" @click="row.toggleDetails" class="mr-2">
              Rszletek {{ row.detailsShowing ? "Elrejtse" : "Mutatsa" }}
            </b-button>
          </template>
          //TODO adatok megvltoztatsa a hash alapjn
          <template #row-details="row">
            <b-card>
              <b-row class="mb-2">
                <b-col sm="2" class="text-sm-right"><b>Hash kd</b></b-col>
                <b-col sm="2" class="text-sm-right"><b>Hitelestett</b></b-col>
                <b-col sm="2" class="text-sm-right"
                  ><b>Hitelests dtuma</b></b-col
                >
                <b-col sm="2" class="text-sm-right"
                  ><b>Hash ellenrztt</b></b-col
                >
                <b-col sm="2" class="text-sm-right"
                  ><b>Hash ellenrzs dtuma</b></b-col
                >
              </b-row>

              <b-row class="mb-2">
                <b-col>{{ row.item.egy }}</b-col
                ><b-col>{{ row.item.ketto ? "&check;" : "&#10060;" }}</b-col>
                <b-col>{{ row.item.harom }}</b-col>
                <b-col>{{ row.item.negy ? "&check;" : "&#10060;" }}</b-col>
                <b-col>{{ row.item.ot }}</b-col>
              </b-row>
            </b-card>
          </template>

          <template #cell(hash)="data"
            >{{ data.item.certificationSigned }}
          </template>
          <template #cell(institution)="data">{{
            data.item.institution
          }}</template>

          <template #cell(qualification)="data"
            >{{ data.item.qualification }}
          </template>

          <template #cell(certificationId)="data"
            >{{ data.item.certificationId }}
          </template>
          <template #cell(participantName)="data">
            {{ data.item.participantName }}</template
          >
          <template #cell(participantBirth)="data">
            {{ data.item.participantBirth }}</template
          >
          <template #cell(qualificationDate)="data"
            >{{ data.item.qualificationDate }}
          </template>
          <template #cell(certificationGrade)="data">
            {{ data.item.certificationGrade }}</template
          >
          <template #cell(certificationDate)="data"
            >{{ data.item.certificationDate }}
          </template>
          <template #cell(certificationSigned)="data"
            >{{ data.item.certificationSigned }}
          </template>
        </b-table>

        <b-pagination
          align="center"
          v-model="currentPage"
          :total-rows="rows"
          :per-page="perPage"
          aria-controls="table"
        ></b-pagination>
        <button class="login-button" id="login-button-style">
          Hash ltrehozsa
        </button>
        <span>{{ errorMessage }}</span>
      </div>
    </div>
  </div>
</template>

